<template>
	<div class="publishProducts">
		<Spin size="large" fix v-if="loading"></Spin>
		<Form ref="products" :model="formData" :rules="rules">
			<FormItem label="" prop="name">
				<Input placeholder="" v-model="formData.name"></Input>
			</FormItem>
			<FormItem label="" prop="tags">
				<Select placeholder="" v-model="formData.tags">
					<Option v-if="tags.length === 0" value="">!</Option>
					<Option v-else v-for="tag in tags" :key="tag._id" :value="tag._id">{{tag.name}}</Option>
				</Select>
			</FormItem>
			<FormItem label="" prop="detail">
				<Input placeholder="" v-model="formData.detail" type="textarea" :autosize="{minRows: 2,maxRows: 3}"></Input>
			</FormItem>
			<FormItem label="()" prop="price">
				<Input placeholder="" number v-model="formData.price"></Input>
			</FormItem>
			<FormItem label="" prop="imgs">
				<upload-img :imgs.sync="formData.imgs" type="avatar" :maxSize="6"></upload-img>
			</FormItem>
			<FormItem label="" prop="banner">
				<upload-img :imgs.sync="formData.banner" type="banner" :maxSize="1"></upload-img>
			</FormItem>
		</Form>
		<Button type="success" class="sub" @click.stop="subForm" :loading="loading"></Button>
	</div>
</template>

<script>
import { publish, findOne, edit } from '@/api/products'
import uploadImg from '@/components/public/uploadImg'
export default {
	data() {
		return {
			loading: false,
			tags: [],
			formData: {
				name: '',
				tags: '',
				detail: '',
				price: '',
				imgs: [],
				banner: []
			},
			rules: {
				name: [
					{ required: true, message: '', trigger: 'blur' }
				],
				tags: [
					{ required: true, message: '', trigger: 'blur' }
				],
				detail: [
					{ required: true, message: '', trigger: 'blur' }
				],
				price: [
					{ required: true, type: 'number', min: 1, message: '', trigger: 'blur' }
				],
				banner: [
					{ required: true, type: 'array', message: '', trigger: 'blur' }
				]
			}
		}
	},
	methods: {
		subForm() {
			this.$refs.products.validate(valid => {
				if (valid) {
					this.loading = true;
					//* 
					const pubProduct = (fn, ...rest) => fn(...rest).then(res => {
						this.$Message.success(res.msg);
						this.$router.push('/index/products');
					}).catch(err => {
						this.loading = false;
						this.$Message.error(err.msg);
					})
					const formData = JSON.parse(JSON.stringify(this.formData));
					formData.banner = formData.banner[0]
					if (this.$route.params.id) {
						pubProduct(edit, formData);
					} else {
						pubProduct(publish, formData);
					}
				} else {
					this.$Message.error('!');
				}
			})
		},
		loadTags() {
			this.$store.dispatch('tags/getTags').then(() => {
				this.tags = this.$store.getters['tags/getTags'];
			});
		}
	},
	created() {
		if (this.$route.params.id) {
			this.loading = true;
			findOne(this.$route.params.id).then(res => {
				res.data.banner = [res.data.banner];
				this.formData = res.data;
				this.loading = false;
			}).catch(err => {
				this.$Message.error(err.msg);
				this.loading = false;
			})
		}
		this.loadTags();
	},
	components: {
		uploadImg
	}
}
</script>

<style lang="scss">
.publishProducts {
	width: 800px;
	margin: 20px auto 0;
}
.inline {
	display: inline-block;
	width: 100%;
}
.smallInput{
	margin-bottom: 10px;
	&:last-child {
		margin: 0;
	}
	.ivu-input-wrapper {
		width: auto;
	}
}
.formLabel {
	display: inline-block;
	width: 80px;
}
.w50 {
	width: 45%;
}
.sub {
	text-align: center;
}
</style>



